/***********************
Section Countdown
***********************/

.countdown-timer {
  position: relative;
  background-image: linear-gradient(to right bottom, #28569ac2, #4ba0e1c9),
    url(/ISSA_Dynamic_Web/static/media/timer.c279807e.webp);
  background-size: cover;
  background-position: top;
  text-align: center;
  height: 120rem;
  -webkit-clip-path: polygon(0 20rem, 100% 0, 100% 100rem, 0 100%);
          clip-path: polygon(0 20rem, 100% 0, 100% 100rem, 0 100%);
  margin-top: -15rem;
}

.countdown-timer__box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
}

.countdown-timer h3 {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 5rem;
  color: #fff;
  text-transform: uppercase;
  font-weight: 300;
}

.countdown-timer a {
  position: absolute;
  top: 75%;
  left: 0.5%;
}

.count {
  width: 19rem;
  height: 22rem;
  background: #fff;
  margin: 4rem 4rem;
  border-radius: 1rem 1rem 0 0;
  line-height: 28rem;
  text-align: center;
  position: relative;
  box-shadow: 0 0.1rem 0.6rem 0 rgba(57, 73, 76, 0.35);
}

.countdown-timer__box .count h4 {
  position: absolute;
  top: 9rem;
  left: 4rem;
  font-weight: 700;
  font-size: 10rem;
}

.countdown-timer__box .days h4 {
  color: #fb502f;
  left: 7rem;
}

.countdown-timer__box .hours h4 {
  color: #3778c2;
}

.countdown-timer__box .minutes h4 {
  color: #ffbd3b;
}

.countdown__header {
  width: 100%;
  height: 7rem;
  border-radius: 1rem 1rem 0 0;
  text-align: center;
  color: #fff;
  line-height: 7.5rem;
  position: absolute;
  font-size: 3rem;
  top: 0;
  left: 0;
  text-transform: uppercase;
}

.red {
  background: #fb502f;
}

.blue {
  background: #3778c2;
}

.yellow {
  background: #ffbd3b;
}

.countdown__header::after {
  content: "";
  position: absolute;
  color: #000;
  width: 3%;
  border-style: solid;
  border-width: 0.3rem;
  background: #000;
  height: 2rem;
  border-radius: 0.5rem;
  top: -15%;
  left: 18%;
}

.countdown__header::before {
  content: "";
  position: absolute;
  color: #000;
  width: 3%;
  border-style: solid;
  border-width: 0.3rem;
  background: #000;
  height: 2rem;
  border-radius: 0.5rem;
  top: -15%;
  left: 74%;
}

@media (max-width: 90rem) {
  .countdown-timer {
    height: 110rem;
    -webkit-clip-path: polygon(0 15rem, 100% 0, 100% 95rem, 0 100%);
            clip-path: polygon(0 15rem, 100% 0, 100% 95rem, 0 100%);
    margin-top: 5rem;
  }

  .countdown-timer h3 {
    font-size: 5rem;
    color: #fff;
