/*******************
Navigation Menu
*******************/

.navigation__checkbox {
  display: none;
}

.navigation__button {
  background-color: #fff;
  height: 7rem;
  width: 7rem;
  position: fixed;
  bottom: 4rem;
  right: 5rem;
  border-radius: 50%;
  z-index: 2000;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
}

.navigation__background {
  height: 6rem;
  width: 6rem;
  border-radius: 50%;
  position: fixed;
  bottom: 4.5rem;
  right: 5.5rem;
  background-image: radial-gradient(#4b9fe1, #28559a);
  z-index: 1000;
  transition: all 0.3s ease-in-out;
}

.navigation__nav {
  height: 100vh;
  position: fixed;
  top: 0;
  left: -50%;
  z-index: 1500;

  opacity: 0;
  width: 0;
  transition: all 0.3s ease-in-out;
}

.navigation__list {
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
  width: 100%;
}

.navigation__item {
  margin: 1rem;
}

.navigation__link:link,
.navigation__link:visited {
  display: inline-block;
  font-size: 3rem;
  font-weight: 300;
  padding: 1rem 2rem;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  background-image: linear-gradient(
    120deg,
    transparent 0%,
    transparent 50%,
    #fff 50%
  );
  background-size: 220%;
  transition: all 0.4s;
}

.navigation__link:link span,
.navigation__link:visited span {
  margin-right: 1.5rem;
  display: inline-block;
}

.navigation__link:hover,
.navigation__link:active {
  background-position: 100%;
  color: #3778c2;
  transform: translateX(1rem);
}

.navigation__checkbox:checked ~ .navigation__background {
  transform: scale(80);
}

.navigation__checkbox:checked ~ .navigation__nav {
  opacity: 1;
  width: 100%;
}

.navigation__icon {
  position: relative;
  margin-top: 3.5rem;
}

.navigation__icon,
.navigation__icon::before,
.navigation__icon::after {
  width: 3rem;
  height: 2px;
  background-color: #333;
  display: inline-block;
}

.navigation__icon::before,
.navigation__icon::after {
  content: "";
  position: absolute;
  left: 0%;
  transition: all 0.2s;
}

.navigation__icon::before {
  top: -0.8em;
}

.navigation__icon::after {
  top: 0.8rem;
}

.navigation__button:hover .navigation__icon::before {
  top: -1rem;
}

.navigation__button:hover .navigation__icon::after {
  top: 1rem;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  top: 0;
  transform: rotate(135deg);
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  top: 0;
  transform: rotate(-135deg);
}

@media (max-width: 60rem) {
  .navigation__link:link,
  .navigation__link:visited {
    font-size: 2.5rem;
  }
}

/*****************
Section team
*****************/

.section-team {
  width: 114rem;
  text-align: center;
  height: 100%;
  margin: 0 auto;
  padding-top: 10rem;
}

.team-flex-box {
  font-family: tahoma;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.our-team {
  width: 27rem;
  height: 35rem;
  padding: 3rem 0 4rem;
  background-color: #f7f5ec;
  text-align: center;
  overflow: hidden;
  position: relative;
  margin: 1rem 2rem;
  transition: all 0.5s;
}

.our-team .picture {
  display: inline-block;
  height: 13rem;
  width: 13rem;
  margin-bottom: 5rem;
  z-index: 1;
  position: relative;
}

.img-fluid {
  max-height: 14rem;
  max-width: 14rem;
}

.our-team .picture::before {
  content: "";
  width: 100%;
  height: 0;
  border-radius: 50%;
  background-color: #1369ce;
  position: absolute;
  bottom: 135%;
  right: 0;
  left: 0;
  opacity: 0.9;
  transform: scale(3);
  transition: all 0.3s linear 0s;
}

.our-team:hover {
  box-shadow: 1rem 1rem 2.2rem -1rem rgba(0, 0, 0, 0.3);
  /* transform: translate(-1px, -2px); */
}

.our-team:hover .picture::before {
  height: 100%;
}

.our-team .picture::after {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #1369ce;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.our-team .picture img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  transform: scale(1);
  transition: all 0.9s ease 0s;
}

.our-team:hover .picture img {
  box-shadow: 0 0 0 1.4rem #f7f5ec;
  transform: scale(0.7);
}

.our-team .title {
  display: block;
  font-size: 1.5rem;
  color: #4e5052;
  text-transform: capitalize;
}

.our-team .social {
  width: 100%;
  padding: 0;
  margin: 0;
  background-color: #1369ce;
  position: absolute;
  bottom: -10rem;
  left: 0;
  transition: all 0.5s ease 0s;
}

.our-team:hover .social {
  bottom: 0;
}

.our-team .social li {
  display: inline-block;
}

.our-team .social li a {
  display: block;
  padding: 10px;
  font-size: 1.7rem;
  color: white;
  transition: all 0.3s ease 0s;
  text-decoration: none;
}

.our-team .social li a:hover {
  color: #1369ce;
  background-color: #f7f5ec;
}

.team-content .name {
  font-size: 2rem;
}

@media (max-width: 90rem) {
  .section-team {
    width: auto;
  }
}

@media (max-width: 50rem) {
  .team-flex-box {
    flex-direction: column;
  }

  .our-team {
    margin: 1rem auto;
  }
}

