.items{
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo1{
    width: 50px;
    height: 40px;
    transition: .8s;
    z-index: 3;
    /* animation: logorotate .8s ease-in-out infinite; */
    display: block;
    margin-bottom: 20px;
}
.logo1:hover{
    transform: rotate(360deg);
}
.wig{
	display: flex;
	padding: 0;
}
.wig li{
  padding: 10px;
	list-style: none;
}
.wig li a{
	width: 40px;
	height: 40px;
	text-align: center;
	line-height: 30px;
	font-size: 16px;
	margin: 0 3px;
	display: block;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
	border: 1px black;
	z-index: 1;
}
.wig li a .fa{
	position: relative;
	color: 	#C8C8C8;
	transition: .5s;
	z-index: 3;
    font-size: 20px;
}
.wig li a:hover .fa{
	color: #fff;
	transform: rotateY(360deg);
	font-size: 23px;
}
.wig li:nth-child(1) a:hover .fa{
    color: #ffffff;
  }
.wig li:nth-child(2) a:hover .fa{
  color: #2ea3f1;
}
.wig li:nth-child(3) a:hover .fa{
    color: #FF0000;
  }
  .wig li:nth-child(4) a:hover .fa{
    color: #C13584;
  }
.wig li:nth-child(5) a:hover .fa{
  color: #df323a;
}
.wig li a:before{
	content: "";
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	/* background: #f00; */
	transition: .5s;
	z-index: 2;
}
.contact{
    background-color: white;
    padding: 5px;
    border-radius: 6px;
    color: black;
    font-weight: 400;
    margin-right: 5px;
    font-size: 1.3rem;
}
.contact:hover{
    background-color: rgb(63, 63, 63);
    text-decoration: none;
    color: white;
    transition: all 0.5s ease-in-out;
}
*{
    box-sizing: border-box;
    scroll-behavior: smooth;
    transition: all .5s ease-in-out;
}
body{
    overflow-x: hidden;
}
a:hover{
    text-decoration: none;
}
.heading-secondary{
    display: flex;
    justify-content: center;
}
.eventpagebackgimg{
    padding: 110px;
    background-image: url(/ISSA_Dynamic_Web/static/media/background-pc.0b0cd2c6.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    transform-style:flat;
    min-height: -webkit-max-content;
    min-height: -moz-max-content;
    min-height: max-content;
    background-size: cover;
    background-position: top;
}
.fulllist{
    padding-top: 10px;
    padding-bottom: 40px;
    margin-bottom: 0px;
}
.onecard{
    margin: 12rem;
    -webkit-animation: moveInLeft 0.8s ease-out;
            animation: moveInLeft 0.8s ease-out;
}
.onecard:nth-last-child(n){
    margin-bottom: 0px;
}
.elistcard{
    margin: auto;
    border-radius: 2rem !important;
}
.elistcard:hover{
    transform: scale(1.1);
  box-shadow: 0px 0px 15px 0px;
}
.elistimg{
    padding-top: 15px;
    width: auto;
    height: 300px;
    border-radius: 2.5rem !important;
}
#background-video{
    height: 400px;
    width: 100%;
    top: 0;
    padding: none;
}
.eventheading{
    display: flex;
    justify-content: center;
}
.ename{
    display: flex;
    justify-content: center;
    font-size: 2rem;
}
.ediscription{
    font-size: 1.5rem;
    text-align: center;
}
.heading-upcoming{
    font-size: 3rem;
    font-weight: bold;
    color: white;
    display: flex;
    justify-content: center;
    margin-top: 80px;
}
.buttonforevent{
    margin: 1rem 8rem;
    display: flex;
    justify-content: center;
    color: white;
    background: linear-gradient(to right, #4b9fe1, #3778c2);
    padding: 8px 30px;
    border-radius: 10px;
    text-decoration: none;
}
.buttonforevent:hover{
    background: #277bdb;
    color: white;
    text-decoration: none;
    cursor: pointer;
}
.iconcontainer{
    display: flex;
    justify-content: center;
}
.icons{
    display: flex;
    justify-content: center;
    font-size: 3em;
    margin: 1rem 3rem;
    color: black;
}
.fa-pen-square{
    margin: 0rem 1rem;
    font-size: 1.6rem;
}
@media screen and (max-width: 960px){
    .onecard{
        margin: 5rem;
    }
    .elistcard{
        margin: 0px;
    }
    .elistimg{
        padding-top: 15px;
        width: 100%;
        height: 250px;
    }
    .ediscription{
        font-size: 1.2rem;
        text-align: center;
    }
    .buttonforevent{
        margin: 1rem 0rem;
        display: flex;
        justify-content: center;
        color: white;
        background: linear-gradient(to right, #4b9fe1, #3778c2);
        padding: 1.5rem 6rem;
        border-radius: 10px;
        text-decoration: none;
    }
    .buttonforevent:hover{
        background: linear-gradient(to left, #4b9fe1, #3778c2);
        color: white;
        text-decoration: none;
    }
    .eventpagebackgimg{
        padding: 0px;
        background-image: url(/ISSA_Dynamic_Web/static/media/background-pc-3.52e5ebcb.jpg);
        background-repeat: repeat;
        background-attachment: local;
        background-size: cover;
    }
}
@media screen and (min-width: 1550px){
    .elistimg{
        padding-top: 15px;
        width: auto;
        height: 750px;
        border-radius: 5rem !important;
    }
    .eventpagebackgimg{
        padding: 130px;
    }
}
